html {
  margin: 0;
  padding: 0;
  font-family: roboto, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-right: 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  padding-left: 50px;
  display: flex;
}

.nav {
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link.is-active {
  margin-left: 0;
}

.nav__link {
  text-align: center;
  color: #000;
  margin-left: 20px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  text-decoration: none;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: #00acdc;
}
